












import Vue from 'vue';

import HelloWorld from '@/components/HelloWorld.vue';
import { delay } from '../util';

export default Vue.extend({
  name: 'game-screen',
  components: {
    HelloWorld,
  },
  data: () => ({ alive: true, digit: null, message: '' }),
  beforeDestroy() {
    this.alive = false;
  },
  async mounted() {
    while (this.alive) {
      const result = await this.$http.get('/game/waiting');

      if (result.status === 204) {
        this.$router.push('/lose');
        return;
      }

      await delay(200 /* milliseconds */);
    }
  },
  methods: {
    async submit() {
      const result = await this.$http.post('/game/guess', {
        digit: this.digit,
      });

      if (result.status === 404) {
        this.$router.push('/lose');
        return;
      }

      const { correct }: { correct: boolean } = result.data;

      if (correct) {
        this.$router.push('/win');
        this.alive = false;
      } else {
        this.message = 'Try again!';
        await delay(300);
        this.message = '';
      }
    },
  },
});
